import { useEffect, useMemo } from 'react'

import { Box, Button, Stack, Typography } from '@mui/material'
import { constants, utils } from 'ethers'
import CountUp from 'react-countup'

import { actions, selectors } from 'context/DApp'

import {
  useAccountInfo,
  useAppDispatch,
  useAppSelector, // useBalances,
  // useLiquidities,
  usePublicInfo,
} from 'hooks/dapp'

import configs, { SupportedTokens } from 'configs'

import InfoCard from './InfoCard'
import InfoTable from './InfoTable'

const FETCH_TOKEN_PRICE_TIMEOUT = 10 * 60 * 1000

export default function Pool() {
  // const liquidities = useLiquidities()
  const dispatch = useAppDispatch()
  const prices = useAppSelector(selectors.prices)
  // const balances = useBalances()

  const publicInfo = usePublicInfo()
  const accountInfo = useAccountInfo()

  const rows = useMemo(
    () =>
      Object.entries(configs.tokens)
        // .entries(publicInfo.core.underlyings)
        .map(([token, { address, symbol, decimals }]) => ({
          token,
          address,
          symbol,
          decimals,
          price: prices[token] ?? 1,
          user: accountInfo.tokens[token] ?? {
            balance: constants.Zero,
            approved: constants.Zero,
          },
          pool: {
            deposit:
              publicInfo.core.underlyings[token]?.deposit ?? constants.Zero,
          },
        })),
    /* eslint-disable-next-line react-hooks/exhaustive-deps */
    [publicInfo.core.underlyings, accountInfo.tokens]
  )

  console.log(prices, rows.map(r => r.price))

  const totalDeposits = Object.entries(publicInfo.core.underlyings)
    .map(([token, { deposit }]) => {
      const price = prices[token] ?? 0
      const amounts = Number(
        utils.formatUnits(
          deposit,
          configs.tokens[token as SupportedTokens].decimals
        )
      )
      return Number.isNaN(amounts) ? 0 : amounts * price
    })
    .reduce((a, b) => a + b, 0)

  // TODO: Add other stakings if have
  const tvl = totalDeposits

  // TODO: Require last date virtual price
  const apr = 120.1988

  // TODO: May dismatch digits
  const userDeposits = Number(
    utils.formatUnits(
      accountInfo.deposited.mul(publicInfo.core.virtualPrice),
      36
    )
  )

  const lastFetchStablecoinStep = Math.round(
    Date.now() / FETCH_TOKEN_PRICE_TIMEOUT
  )

  useEffect(() => {
    dispatch(actions.fetchStablecoinPrices())
  }, [dispatch, lastFetchStablecoinStep])

  return (
    <Stack py={12.5} direction="column" spacing={2}>
      <Box className="tvl">
        <Typography
          variant="h5"
          textAlign="center"
          display="block"
          lineHeight={1}
        >
          Market Total Value
        </Typography>
        <Typography variant="TVL">
          <CountUp
            end={tvl}
            separator=","
            decimals={2}
            prefix="$ "
            delay={1.5}
            duration={0.4}
          />
        </Typography>
      </Box>

      <Stack direction="row" spacing={2.5}>
        <InfoCard title="Total Deposits">
          <CountUp
            end={totalDeposits}
            separator=","
            decimals={2}
            prefix="$ "
            delay={1.2}
            duration={0.4}
          />
        </InfoCard>

        <InfoCard title="APR">
          <CountUp
            end={apr}
            decimals={4}
            suffix=" %"
            delay={0.2}
            duration={0.3}
          />
        </InfoCard>

        <InfoCard title="My Total Deposits">
          <CountUp
            end={userDeposits}
            separator=","
            decimals={2}
            prefix="$ "
            delay={0.6}
            duration={0.4}
          />
        </InfoCard>
      </Stack>

      <Typography variant="h5" pt={5.5}>
        Deposit Pool
      </Typography>

      <InfoTable rows={rows}>
        <Stack direction="row" spacing={2.5} width="100%">
          <Box flexBasis={200} flexGrow={1}>
            <Button fullWidth size="large" variant="contained" color="primary">
              Deposit
            </Button>
          </Box>
          <Box flexBasis={200} flexGrow={1}>
            <Button fullWidth size="large" variant="outlined" color="primary">
              Withdraw
            </Button>
          </Box>
        </Stack>
      </InfoTable>
    </Stack>
  )
}
