import Avatar, { AvatarProps } from '@mui/material/Avatar'

import BUSD from 'assets/tokens/busd.svg'
import USDC from 'assets/tokens/usdc.svg'
import USDT from 'assets/tokens/usdt.svg'

type Tokens = {
  [symbol: string]: string
}

type Styles = {
  width: number
  height: number
}

interface Sizes {
  sm: Styles
  lg: Styles
}

const tokens: Tokens = {
  USDT,
  USDC,
  BUSD,
}

const sizes: Sizes = {
  sm: { width: 24, height: 24 },
  lg: { width: 40, height: 40 },
}

interface TokenIconProps extends AvatarProps {
  token: keyof Tokens
  size?: keyof Sizes
}

export default function TokenIcon({
  token,
  size,
  sx = {},
  ...rest
}: TokenIconProps) {
  const icon = tokens[token]
  const styles = size ? { ...(sizes[size]), ...sx } : null

  return (
    <Avatar
      src={icon}
      alt={token as string}
      variant="square"
      sx={styles}
      {...rest}
    />)
}
