import { useCallback, useState } from 'react'
import type { ReactNode } from 'react'

import ArrowDownward from '@mui/icons-material/ArrowDownward'
import {
  Button,
  IconButton,
  Paper,
  SelectChangeEvent,
  Stack,
  Typography,
} from '@mui/material'
import type { PaperProps } from '@mui/material'
import Box from '@mui/material/Box'
import { styled } from '@mui/material/styles'

import Frame from 'components/Frame'
import Middle from 'components/Middle'
import TokenInput from 'components/TokenInput'
import TokenSelect from 'components/TokenSelect'
import { useBalances } from 'hooks/dapp'
import { displayBalanceWithDigits } from 'utils/helpers'

import configs, { fromTokenAddress } from 'configs'

import Slogan from './Slogan'

const displayBalance = displayBalanceWithDigits(4)
const SwapCard = styled(Paper)<PaperProps>(() => ({
  zIndex: 1,
  boxShadow: `0px 0px 20px rgba(21, 81, 0, 0.94)`,
  padding: 20,
}))

const tokenAddresses = Object.values(configs.tokens).map(
  (token) => token.address
)

export default function Swap() {
  const [[swapIn, swapOut], setTokens] = useState(tokenAddresses.slice(0, 2))

  const balances = useBalances()
  const [tokenIn, tokenOut] = [
    fromTokenAddress[swapIn],
    fromTokenAddress[swapOut],
  ]

  const onSwapInTokenChange = useCallback(
    (ev: SelectChangeEvent<unknown>, _: ReactNode) => {
      const newSwapIn = ev.target.value as string
      if (newSwapIn === swapOut) {
        return setTokens([swapOut, swapIn])
      }
      setTokens([newSwapIn, swapOut])
    },
    [swapIn, swapOut]
  )

  const onSwapOutTokenChange = useCallback(
    (ev: SelectChangeEvent<unknown>, _: ReactNode) => {
      const newSwapOut = ev.target.value as string
      if (newSwapOut === swapIn) {
        return setTokens([swapOut, swapIn])
      }
      setTokens([swapIn, newSwapOut])
    },
    [swapIn, swapOut]
  )

  const onSwapExchange = useCallback(
    () => setTokens([swapOut, swapIn]),
    [swapIn, swapOut]
  )

  return (
    <Stack py={18} direction="column" spacing={2}>
      <Middle width={420}>
        <Stack
          width={420}
          spacing={2}
          pt={6}
          position="relative"
          overflow="hidden"
          borderRadius="20px"
        >
          <Slogan>Stablecoin Swap: Low Slippage + Low Transaction Fee</Slogan>
          <SwapCard elevation={17}>
            <Stack>
              <Frame>
                <Stack direction="row">
                  <Box>
                    <Typography variant="subject">From</Typography>
                  </Box>
                  <Box flexGrow={1} textAlign="right">
                    <Typography variant="info">
                      Balance: &nbsp;
                      {displayBalance(
                        balances[tokenIn],
                        configs.tokens[tokenIn].decimals
                      )}
                      &nbsp;
                      {configs.tokens[tokenIn].symbol}
                    </Typography>
                  </Box>
                </Stack>
                <Stack direction="row" alignItems="center">
                  <TokenInput
                    sx={{ mr: 0.5, flexGrow: 1 }}
                    onMax={() => {
                      console.log('max')
                    }}
                  />
                  <Box flexBasis={120}>
                    <TokenSelect
                      onChange={onSwapInTokenChange}
                      value={swapIn}
                    />
                  </Box>
                </Stack>
              </Frame>
              <Stack direction="row" py={1}>
                <Box flexGrow={1} />
                <IconButton
                  color="primary"
                  size="small"
                  onClick={onSwapExchange}
                >
                  <ArrowDownward fontSize="inherit" />
                </IconButton>
                <Box flexGrow={1} />
              </Stack>
              <Frame>
                <Stack direction="row">
                  <Box>
                    <Typography variant="subject">To</Typography>
                  </Box>
                  <Box flexGrow={1} textAlign="right">
                    <Typography variant="info">
                      Balance: &nbsp;
                      {displayBalance(
                        balances[tokenOut],
                        configs.tokens[tokenOut].decimals
                      )}
                      &nbsp;
                      {configs.tokens[tokenOut].symbol}
                    </Typography>
                  </Box>
                </Stack>
                <Stack direction="row" alignItems="center">
                  <TokenInput sx={{ mr: 0.5, flexGrow: 1 }} />
                  <Box flexBasis={120}>
                    <TokenSelect
                      onChange={onSwapOutTokenChange}
                      value={swapOut}
                    />
                  </Box>
                </Stack>
              </Frame>
              <Box height={54}></Box>
              <Button
                variant="contained"
                color="primary"
                size="large"
                sx={{ fontWeight: 600 }}
              >
                SWAP
              </Button>
            </Stack>
          </SwapCard>
        </Stack>
      </Middle>

      <Middle width={420}>
        <Paper sx={{ padding: 2.5 }} elevation={17}>
          <Stack direction="row">
            <Box flexGrow={11}>
              <Typography variant="info">
                Exchange Rate USDT.b/BUDS.b (including fees)
              </Typography>
            </Box>
            <Box flexGrow={1} textAlign="right">
              <Typography variant="detail">1:1</Typography>
            </Box>
          </Stack>
          <Stack direction="row">
            <Box flexGrow={11}>
              <Typography variant="info">Max slippage</Typography>
            </Box>
            <Box flexGrow={1} textAlign="right">
              <Typography variant="detail">0.3%</Typography>
            </Box>
          </Stack>
        </Paper>
      </Middle>
    </Stack>
  )
}
