import { useCallback, useMemo } from 'react'

// import WalletConnectProvider from '@walletconnect/web3-provider'
import Button from '@mui/material/Button'
import {
  shortenAddress, // useEtherBalance,
  useEthers,
} from '@usedapp/core'

import configs from 'configs'

import { ReactComponent as IconImToken } from 'assets/wallets/logos/imtoken.svg'
import { ReactComponent as IconMetaMask } from 'assets/wallets/logos/metamask.svg'
import { ReactComponent as IconTrustWallet } from 'assets/wallets/logos/trust.svg'

const InjectedWallets = {
  isMetaMask: IconMetaMask,
  isTrust: IconTrustWallet,
  isImToken: IconImToken,
}

export default function ConnectButton() {
  const { activateBrowserWallet, account, deactivate, chainId, switchNetwork } =
    useEthers()

  const walletIcon = useMemo(() => {
    if (account) {
      const [, Logo] =
        Object.entries(InjectedWallets)
          // @ts-ignore
          .find(([verifier]) => window.ethereum?.[verifier] ?? false) ?? []

      if (Logo) return <Logo className="wallet-logo" />
    }
    return null
  }, [account])

  const changeNetwork = useCallback(() => {
    switchNetwork(configs.network.chainId)
  }, [switchNetwork])

  if (chainId && chainId !== configs.network.chainId) {
    return (
      <Button
        className="connect-button"
        variant="contained"
        color="error"
        onClick={changeNetwork}
        sx={{
          borderRadius: 2,
          width: 166,
        }}
      >
        Wrong Network
      </Button>
    )
  }

  if (account) {
    return (
      <Button
        className="connect-button"
        variant="outlined"
        color="primary"
        onClick={deactivate}
        sx={{
          borderRadius: 2,
          borderColor: 'rgba(113, 255, 58, .5)',
          width: 166,
        }}
      >
        {shortenAddress(account)}
        {walletIcon}
      </Button>
    )
  }

  return (
    <Button
      variant="contained"
      color="primary"
      onClick={activateBrowserWallet}
      sx={{ width: 166 }}
    >
      Connect Wallet
    </Button>
  )
}
