import GA from 'react-ga4'

const trackingID = process.env.REACT_APP_GA
if (trackingID) {
  if (process.env.NODE_ENV === 'production') {
    GA.initialize(trackingID)
  } else {
    GA.initialize('test', { testMode: true })
  }
}

export const sendEvent = (category: string, label: string, action: string = 'button') =>
  GA.event({
    category,
    action,
    label,
  })
