import { useMemo } from 'react'
import type { FC } from 'react'

import Button from '@mui/material/Button'
import InputAdornment from '@mui/material/InputAdornment'
import InputBase from '@mui/material/InputBase'
import type { InputBaseProps } from '@mui/material/InputBase'
import { styled } from '@mui/material/styles'

const TokenAmountInput = styled(InputBase)({
  fontFamily: ['Kanit', 'sans-serif'].join(','),
  fontWeight: 600,
  fontSize: 24,
  lineHeight: 1,
  color: '#FFF',
  letterSpacing: 0.48,
})

const MaxButton = styled(Button)({
  width: 44,
  height: 24,
  borderRadius: 8,
  backgroundColor: 'rgba(113, 255, 58, .2)',
  fontSize: 14,
  lineHeight: '24px',
  padding: '0 6px',
  minWidth: 44,
})

interface TokenInputProps extends InputBaseProps {
  onMax?: () => void
}

const TokenInput: FC<TokenInputProps> = ({ onMax, ...props }) => {
  const endAdornment = useMemo(() => {
    if (typeof onMax !== 'function') return null

    return (
      <InputAdornment position="end">
        <MaxButton size="small" onClick={onMax}>
          MAX
        </MaxButton>
      </InputAdornment>
    )
  }, [onMax])

  return (
    <TokenAmountInput
      endAdornment={endAdornment}
      placeholder="0.00"
      {...props}
    />
  )
}

export default TokenInput
