const InfoTableHeader = styled(CardHeader)({
  backgroundColor: '#181818',
  padding: '14px 24px',
})

const InfoTableBody = styled(CardContent)({
  padding: '8px 24px 0',
  '&::after': {
    content: '" "',
    width: '100%',
    display: 'table',
    borderBottom: '1px solid rgba(255,255,255,.1)',
  },
})

const InfoTableFooter = styled(CardActions)({
  padding: '40px 24px',
})

const ColumnWidth = [320, 380, 260, 200]

interface InfoTableRow {
  token: string
  address: string
  symbol: string
  decimals: number
  price: number
  user: {
    balance: BigNumber
    approved: BigNumber
  }
  pool: {
    deposit: BigNumber
  }
}

interface InfoTableProps extends CardActionsProps {
  rows: InfoTableRow[]
}

const InfoTable: FC<InfoTableProps> = ({ rows, children }) => {
  return (
    <Card sx={{ borderRadius: 2.5 }}>
      <InfoTableHeader
        title={
          <Stack direction="row" spacing={2}>
            <Box flexBasis={ColumnWidth[0]} flexGrow={1} flexShrink={1} />
            <Box flexBasis={ColumnWidth[1]} flexGrow={1} flexShrink={0}>
              <Typography variant="cardHeader">Pool Deposits</Typography>
            </Box>
            <Box flexBasis={ColumnWidth[2]} flexGrow={1} flexShrink={0}>
              <Typography variant="cardHeader">My Max Withdrables</Typography>
            </Box>
            <Box
              flexBasis={ColumnWidth[3]}
              flexGrow={1}
              flexShrink={0}
              textAlign="right"
            >
              <Typography variant="cardHeader">Wallet Balance</Typography>
            </Box>
          </Stack>
        }
      />
      <InfoTableBody>
        {rows.map((row) => (
          <Stack
            direction="row"
            key={row.token}
            alignItems="center"
            sx={{ height: 80 }}
            spacing={2}
          >
            <Stack
              flexBasis={ColumnWidth[0]}
              flexGrow={1}
              flexShrink={1}
              direction="row"
              spacing={2}
              alignItems="center"
            >
              <TokenIcon token={row.token} size="lg" />
              <Typography variant="tableRowSubject">{row.symbol}</Typography>
            </Stack>
            <Stack
              flexBasis={ColumnWidth[1]}
              flexGrow={1}
              flexShrink={0}
              direction="column"
            >
              <Typography component="p" variant="tableCellMajor">
                {utils.formatUnits(row.pool.deposit, row.decimals)}
                &nbsp;
                {row.symbol}
              </Typography>
              <Typography component="p" variant="tableCellMinor">
                {(row.price *
                  Number(utils.formatUnits(row.pool.deposit, row.decimals))).toFixed(3)}
              </Typography>
            </Stack>
            <Stack
              flexBasis={ColumnWidth[2]}
              flexGrow={1}
              flexShrink={0}
              direction="column"
            >
              <Typography component="p" variant="tableCellMajor">
                777 &nbsp; {row.symbol}
              </Typography>
              <Typography component="p" variant="tableCellMinor">
                $ {(row.price * 777).toFixed(3)}
              </Typography>
            </Stack>
            <Stack
              flexBasis={ColumnWidth[3]}
              flexGrow={1}
              flexShrink={0}
              direction="column"
            >
              <Typography
                component="p"
                variant="tableCellMajor"
                textAlign="right"
              >
                {utils.formatUnits(row.user.balance, row.decimals)}
                &nbsp;
                {row.symbol}
              </Typography>
              <Typography
                component="p"
                variant="tableCellMinor"
                textAlign="right"
              >
                {(
                  row.price *
                  Number(utils.formatUnits(row.user.balance, row.decimals))
                ).toFixed(3)}
              </Typography>
            </Stack>
          </Stack>
        ))}
      </InfoTableBody>
      <InfoTableFooter>{children}</InfoTableFooter>
    </Card>
  )
}

export default InfoTable
