import React from 'react'

import { Config, DAppProvider, DEFAULT_SUPPORTED_CHAINS } from '@usedapp/core'
import { Provider as ReduxProvider } from 'react-redux'

import { FindoraMainnet, FindoraTestnet } from './chains'
import store from './store'

const config: Config = {
  readOnlyChainId: Number(
    process.env.REACT_APP_NETWORK_ID ?? FindoraMainnet.chainId
  ),
  readOnlyUrls: {
    [`${FindoraMainnet.chainId}`]: FindoraMainnet.rpcUrl as string,
    [`${FindoraTestnet.chainId}`]: FindoraTestnet.rpcUrl as string,
  },
  networks: [...DEFAULT_SUPPORTED_CHAINS, FindoraMainnet, FindoraTestnet],
}

const DAppWrapper: React.FC<{
  children: React.ReactNode | React.ReactNode[]
}> = ({ children }) => (
  <DAppProvider config={config}>
    <ReduxProvider store={store}>{children}</ReduxProvider>
  </DAppProvider>
)

export default DAppWrapper
