import { useMemo } from 'react'

import {
  Call,
  useCall,
  useCalls,
  useContractFunction,
  useEthers,
  useTokenBalance,
} from '@usedapp/core'
import { BigNumber, constants } from 'ethers'
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux'

import configs, { fromTokenAddress } from 'configs'
import { Calimables, CorePool, Tokens } from 'contracts'

import type { AppDispatch, RootState } from '../context/DApp/store'

export const useAppDispatch = () => useDispatch<AppDispatch>()
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector

const noResult: any[] = []
export const useAccountInfo = () => {
  const { account } = useEthers()
  const results =
    useCalls(
      account
        ? Object.values(Tokens).reduce((calls: Call[], erc20) => {
            return calls.concat([
              {
                contract: erc20,
                method: 'balanceOf',
                args: [account],
              },
              {
                contract: erc20,
                method: 'allowance',
                args: [account, configs.core.pool.address],
              },
            ])
          }, [
            { contract: CorePool, method: 'balanceOf', args: [account] },
          ])
        : []
    ) ?? noResult

  return useMemo(() => {
    return {
      account,
      deposited: results[0]?.value[0] ?? constants.Zero,
      tokens: Object.fromEntries(
        Object.keys(Tokens).map((token, i) => {
          const [balance, approved] = results.slice(i * 2 + 1, i * 2 + 3)
          return [token, {
            balance: balance?.value[0] ?? constants.Zero,
            approved: approved?.value[0] ?? constants.Zero,
          }]
        })
      ),
    }
    /* eslint-disable-next-line react-hooks/exhaustive-deps */
  }, [results])
}

export const usePublicInfo = () => {
  const stableCoinCalls = Object.values(Tokens).reduce(
    (calls: Call[], erc20) => {
      return calls.concat([
        {
          contract: erc20,
          method: 'balanceOf',
          args: [configs.core.pool.address],
        },
      ])
    },
    []
  )

  const results =
    useCalls([
      { contract: CorePool, method: 'totalSupply', args: [] },
      { contract: CorePool, method: 'get_virtual_price', args: [] },
      { contract: CorePool, method: 'getBalances', args: [] },
      { contract: CorePool, method: 'getCoins', args: [] },
      ...stableCoinCalls,
    ]) ?? noResult

  return useMemo(() => {
    const [
      resTotalSupply,
      resVirtualPrice,
      resDeposits, // Balances excluded protocol balances
      resCoins,
      ...resTokenBalances
    ] = results

    const balances = Object.fromEntries(
      Object.values(Tokens).map((contract, i) => [
        contract.address,
        resTokenBalances[i]?.value[0] ?? constants.Zero,
      ])
    )

    const underlyings = resCoins?.value
      ? Object.fromEntries(
          (resCoins?.value[0] as string[]).map((address: string, i: number) => [
            fromTokenAddress[address],
            {
              address,
              deposit:
                (resDeposits?.value[0] ?? ([] as BigNumber[]))[i] ??
                constants.Zero,
              balance: balances[address] ?? constants.Zero,
            },
          ])
        )
      : {}

    return {
      core: {
        totalSupply: resTotalSupply?.value[0] ?? constants.Zero,
        virtualPrice: resVirtualPrice?.value[0] ?? constants.Zero,
        underlyings,
      },
      error: [
        resTotalSupply?.error,
        resVirtualPrice?.error,
        resDeposits?.error,
        resCoins?.error,
      ]
        .filter(Boolean)
        .join('; '),
    }
    /* eslint-disable-next-line react-hooks/exhaustive-deps */
  }, [results])
}

export const useClaimables = (token: 'BUSD' | 'USDT' | 'USDC') => {
  const { state, send, resetState } = useContractFunction(
    Calimables[token],
    'claim',
    { transactionName: `Claim${token}` }
  )

  const { status, errorCode, errorMessage } = state

  return {
    claim: send,
    status,
    errorCode,
    errorMessage,
    resetState,
  }
}

const noBalances = Object.fromEntries(
  Object.keys(configs.tokens).map((token) => [token, constants.Zero])
)

export const useBalances = () => {
  const { account } = useEthers()

  const coreBalance = useTokenBalance(configs.core.pool.address, account)

  const balances = Object.fromEntries(
    Object.entries(configs.tokens).map(([token, { address }]) => {
      // eslint-disable-next-line
      const balance = useTokenBalance(address, account)
      return [token, balance ?? constants.Zero]
    })
  )

  if (!account) return noBalances

  return {
    core: coreBalance ?? constants.Zero,
    ...balances,
  }
}

export const useLiquidities = () => {
  return Object.fromEntries(
    Object.entries(configs.tokens).map(([token, { address }]) => {
      // eslint-disable-next-line
      const balance = useTokenBalance(address, configs.core.pool.address)
      return [token, balance ?? constants.Zero]
    })
  )
}

export const useVirtualPrice = () => {
  const { value, error } =
    useCall({
      contract: CorePool,
      method: 'get_virtual_price',
      args: [],
    }) ?? {}

  if (error) {
    console.error(error)
    return undefined
  }
  return value?.[0]
}
