import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
// import { ethers } from 'ethers'
// import type { BigNumber } from 'ethers'

import { RootState } from '../store'
import { tokens, getPrices } from './coingeckoAPI'

export interface TokenState {
  status: 'idle' | 'loading' | 'failed'
  prices: {
    [tokens: string]: number
  }
}

const initialState: TokenState = {
  status: 'idle',
  prices: Object.fromEntries(
    tokens.map((token) => [token, 0])
  ),
}

export const fetchStablecoinPrices = createAsyncThunk(
  'token/fetch-prices',
  async () => getPrices()
)

export const tokenSlice = createSlice({
  name: 'token',
  initialState,
  reducers: {
    // incrementPrice: (state) => {
    //   // Redux Toolkit allows us to write "mutating" logic in reducers. It
    //   // doesn't actually mutate the state because it uses the Immer library,
    //   // which detects changes to a "draft state" and produces a brand new
    //   // immutable state based off those changes
    //   state.info.virtualPrice = state.info.virtualPrice.add(1)
    // },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchStablecoinPrices.pending, (state) => {
        state.status = 'loading'
      })
      .addCase(fetchStablecoinPrices.fulfilled, (state, action) => {
        state.status = 'idle'
        state.prices = action.payload
      })
      .addCase(fetchStablecoinPrices.rejected, (state) => {
        state.status = 'failed'
      })
  },
})

// export const { incrementPrice, decrementPrice, incrementByAmount } =
//   tokenSlice.actions

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const prices = (state: RootState) => state.token.prices

// We can also write thunks by hand, which may contain both sync and async logic.
// Here's an example of conditionally dispatching actions based on current state.
// export const incrementIfOdd =
//   (amount: number): AppThunk =>
//   (dispatch, getState) => {
//     const currentValue = virtualPrice(getState())
//     if (currentValue.mod(2).toNumber() === 1) {
//       dispatch(incrementByAmount(amount))
//     }
//   }

export default tokenSlice.reducer
