interface TokenInfo {
  symbol: string
  address: string
  decimals: number
}

const configs = {
  version: process.env.REACT_APP_VERSION ?? '0.0.0',

  network: {
    chainId: Number(process.env.REACT_APP_NETWORK_ID ?? '2152'),
    rpcUrl:
      process.env.REACT_APP_NETWORK_URL ??
      'https://prod-mainnet.prod.findora.org:8545',
  },

  rollbar: {
    accessToken: process.env.REACT_APP_ROLLBAR_ACCESS_TOKEN ?? '',
  },

  'google-analytics': {
    id: process.env.REACT_APP_GA ?? '',
  },

  core: {
    pool: {
      address: '0x822C7Dd033c9FC0639fA00658CCc546819D50f22',
    },
  },

  tokens: {
    BUSD: {
      symbol: 'BUSD.q',
      address: '0x8c5C03b4913eF7C3F58AF4B2CAAb7eEEb172D3F0',
      decimals: 18,
    },
    USDT: {
      symbol: 'USDT.q',
      address: '0xb15C7cf4cb4ED59e9b7878608d16CeAb794BCcA0',
      decimals: 18,
    },
    USDC: {
      symbol: 'USDC.q',
      address: '0xBc72cfE7D9BBB37b1d6C0290F8BC95753c0A2C2f',
      decimals: 18,
    },
  } as {
    BUSD: TokenInfo
    USDT: TokenInfo
    USDC: TokenInfo
  },
}

export default configs

export type SupportedTokens = keyof typeof configs.tokens

export const fromTokenAddress = Object.fromEntries(
  Object.entries(configs.tokens).map(([token, { address }]) => [
    address,
    token,
  ])
) as {
  [address: string]: SupportedTokens
}
