import axios from 'axios'

const BaseURL = `https://api.coingecko.com/api/v3`

const tokensFromId = {
  'binance-usd': 'BUSD',
  binancecoin: 'BNB',
  ethereum: 'ETH',
  findora: 'FRA',
  tether: 'USDT',
  'usd-coin': 'USDC',
}

export const tokens = Object.values(tokensFromId)

interface SimplePriceResponse {
  [tokenId: string]: {
    usd: number
    // usd_24h_change: number
  }
}

export type TokenID = keyof typeof tokensFromId

export function getPrices() {
  return axios
    .get(`${BaseURL}/simple/price`, {
      params: {
        ids: Object.keys(tokensFromId).join(','),
        vs_currencies: 'usd',
        // include_24hr_change: true,
      },
    })
    .then(({ data }: { data: SimplePriceResponse }) => {
      return Object.fromEntries(
        Object.entries(data).map(([id, { usd }]) => [
          tokensFromId[id as TokenID] ?? id,
          usd,
        ])
      )
    })
}
