import { forwardRef, useCallback, useState } from 'react'
import type { ChangeEventHandler, ReactNode } from 'react'

import {
  Alert,
  AlertProps,
  Button,
  CircularProgress,
  Paper,
  SelectChangeEvent,
  Snackbar,
  Stack,
  Typography,
} from '@mui/material'
import type { PaperProps } from '@mui/material'
import Box from '@mui/material/Box'
import { styled } from '@mui/material/styles'
import { constants, utils } from 'ethers'

import Frame from 'components/Frame'
import Middle from 'components/Middle'
import TokenInput from 'components/TokenInput'
import TokenSelect from 'components/TokenSelect'
import { useBalances, useClaimables } from 'hooks/dapp'
import { displayBalanceWithDigits } from 'utils/helpers'

import configs, { fromTokenAddress } from 'configs'

import Slogan from './Slogan'

const SwapCard = styled(Paper)<PaperProps>(() => ({
  zIndex: 1,
  boxShadow: `6px 0 20px #6d0767e6`,
  padding: 20,
}))

const tokenAddresses = Object.values(configs.tokens).map(
  (token) => token.address
)

const SnackbarContent = forwardRef<HTMLDivElement, AlertProps>(
  function SnackbarContent(props, ref) {
    return <Alert elevation={6} ref={ref} variant="filled" {...props} />
  }
)

const ButtonStatusMapping = {
  None: 'warning',
  PendingSignature: 'info',
  Mining: 'info',
  Success: 'success',
  Exception: 'error',
  Fail: 'error',
} as { [txStatus: string]: 'success' | 'info' | 'warning' | 'error' }

const displayBalance = displayBalanceWithDigits(3)

export default function Claim() {
  const [tokenAddress, setToken] = useState(tokenAddresses[0])
  const [amount, setAmount] = useState('1000.00')
  const token = fromTokenAddress[tokenAddress]
  const { status, claim, errorMessage, resetState } = useClaimables(token)

  const balances = useBalances()

  const onTokenChange = useCallback(
    (ev: SelectChangeEvent<unknown>, _: ReactNode) => {
      setToken(ev.target.value as string)
    },
    []
  )

  const onAmountChange: ChangeEventHandler<HTMLInputElement> = useCallback(
    (ev) => {
      const value = ev.target.value.trim()
      if (!value) return setAmount('')
      if (!Number.isNaN(Number(value))) setAmount(value)
    },
    []
  )

  const handleClaim = useCallback(() => {
    const { decimals } = configs.tokens[token]
    return claim(utils.parseUnits(amount, decimals))
  }, [token, claim, amount])

  const handleSuccess = useCallback(() => {
    setAmount('0.00')
    resetState()
  }, [resetState])

  const buttonContent = ((status: string) => {
    switch (status) {
      case 'PendingSignature':
        return (
          <>
            <CircularProgress size={24} color="warning" sx={{ mr: 1 }} />
            Signing...
          </>
        )
      case 'Mining':
        return (
          <>
            <CircularProgress size={24} color="info" sx={{ mr: 1 }} />
            Waiting for Confirmations...
          </>
        )
      default:
        return 'CLAIM'
    }
  })(status)

  return (
    <Stack py={18} direction="column" spacing={2}>
      <Middle width={420}>
        <Stack
          width={420}
          spacing={2}
          pt={6}
          position="relative"
          overflow="hidden"
          borderRadius="20px"
        >
          <Slogan>Claim example stablecoins for DEMO usage</Slogan>
          <SwapCard elevation={17}>
            <Stack>
              <Frame>
                <Stack direction="row">
                  <Box>
                    <Typography variant="subject">Claim</Typography>
                  </Box>
                  <Box flexGrow={1} textAlign="right">
                    <Typography variant="info">
                      Balance: &nbsp;
                      {displayBalance(
                        balances[token] ?? constants.Zero,
                        configs.tokens[token].decimals
                      )}
                      &nbsp;
                      {configs.tokens[token].symbol}
                    </Typography>
                  </Box>
                </Stack>
                <Stack direction="row" alignItems="center">
                  <TokenInput
                    sx={{ mr: 0.5, flexGrow: 1 }}
                    value={amount}
                    onChange={onAmountChange}
                  />
                  <Box flexBasis={120}>
                    <TokenSelect
                      onChange={onTokenChange}
                      value={tokenAddress}
                    />
                  </Box>
                </Stack>
              </Frame>
              <Box height={54}></Box>
              <Button
                variant="contained"
                color={ButtonStatusMapping[status] ?? 'warning'}
                size="large"
                sx={{ fontWeight: 600 }}
                disabled={!amount || status !== 'None'}
                onClick={handleClaim}
              >
                {buttonContent}
              </Button>
            </Stack>
          </SwapCard>
        </Stack>
      </Middle>
      <Snackbar
        open={status === 'Exception' || status === 'Fail'}
        onClose={resetState}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
        autoHideDuration={5000}
      >
        <SnackbarContent severity="error" onClose={resetState}>
          {errorMessage}
        </SnackbarContent>
      </Snackbar>
      <Snackbar
        open={status === 'Success'}
        onClose={handleSuccess}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
        autoHideDuration={2000}
      >
        <SnackbarContent severity="success" onClose={handleSuccess}>
          Claimed
        </SnackbarContent>
      </Snackbar>
    </Stack>
  )
}
