import ExpandMore from '@mui/icons-material/ExpandMore'
import FormControl from '@mui/material/FormControl'
import MenuItem from '@mui/material/MenuItem'
import Select from '@mui/material/Select'
import type { SelectProps } from '@mui/material/Select'
import Typography from '@mui/material/Typography'
import { styled } from '@mui/material/styles'

import configs, { SupportedTokens } from 'configs'

import TokenIcon from './TokenIcon'

const TokenSeletor = styled(Select)({
  width: 134,

  '.MuiOutlinedInput-notchedOutline': {
    borderWidth: 0,
  },

  '.MuiSelect-select.MuiInputBase-input': {
    paddingRight: 24,
    paddingLeft: 6,
  },

  '.MuiSelect-icon': {
    right: -2,
  },
})

const Tokens = configs.tokens

interface TokenSelectProps extends SelectProps {
  value: typeof Tokens[SupportedTokens]['address']
}

export default function TokenSelect(props: TokenSelectProps) {
  return (
    <FormControl sx={{ m: 0, minWidth: 120 }} size="small">
      <TokenSeletor IconComponent={ExpandMore} {...props}>
        {Object.entries(Tokens).map(([token, { symbol, address }]) => (
          <MenuItem key={token} value={address}>
            <Typography variant="token">
              <TokenIcon token={token} size="sm" />
              &nbsp;&nbsp;
              {symbol}
            </Typography>
          </MenuItem>
        ))}
      </TokenSeletor>
    </FormControl>
  )
}
