import AppBar from '@mui/material/AppBar'
import Tab from '@mui/material/Tab'
import Tabs from '@mui/material/Tabs'
import Toolbar from '@mui/material/Toolbar'
import { styled } from '@mui/material/styles'
import { useLocation, useNavigate } from 'react-router-dom'

import ConnectButton from './ConnectButton'

import { ReactComponent as Logo } from 'assets/logo.svg'

import './header.scss'

const tabs = {
  '/swap': 'swap',
  '/pool': 'pool',
  '/claim': 'claim',
} as { [pathname: string]: string }

const HeaderWrapper = styled(AppBar)({
  borderBottom: '1px solid rgba(255, 255, 255, .1)',
  backgroundImage: `linear-gradient(180deg, rgba(0,0,0,.8) 1%, rgba(0,0,0,.5) 30%, rgba(0,0,0,.6) 70%, rgba(0,0,0,.7) 95%)`,
  backdropFilter: `blur(2px)`,
  filter: `drop-shadow(0 2px 4px #000)`,
})

export default function Header() {
  const loc = useLocation()
  const nav = useNavigate()

  const tab = tabs[loc.pathname] ?? 'swap'

  return (
    <HeaderWrapper
      className="futuristic-header"
      position="sticky"
      color="transparent"
    >
      <Toolbar>
        <Logo width={128} height={36} className="logo" />
        <Tabs
          textColor="primary"
          value={tab}
          onChange={(_, to) => nav(to)}
          centered
          sx={{ flexGrow: 1 }}
        >
          <Tab label="Swap" value="swap" />
          <Tab label="Pool" value="pool" />
          <Tab
            sx={{
              fontSize: 14,
              color: `rgba(255,255,255,.8)`,
            }}
            label="Claim Stablecoins"
            value="claim"
          />
        </Tabs>
        <ConnectButton />
      </Toolbar>
    </HeaderWrapper>
  )
}
